
.settingPay {
  width: 100%;
  margin: 0 auto;
  text-align: left;
}
.settingPay .titleS {
  width: 96%;
  margin: 0 auto;
  font-size: 0.32rem;
  display: block;
  height: 1.0667rem;
  color: #666666;
  line-height: 1.0667rem;
}
.settingPay .settingContent {
  -webkit-box-shadow: 0 0 0.2133rem 0rem #dadada;
          box-shadow: 0 0 0.2133rem 0rem #dadada;
  border-radius: 0.1333rem;
  width: 96%;
  margin: 0 auto;
  height: auto;
  background: #FFF;
}
.settingPay .settingContent table {
  margin: 0 auto;
  font-size: 0.3733rem;
  width: 91%;
}
.settingPay .settingContent table tr th {
  height: 1.36rem;
  border-bottom: 0.0267rem solid #EEEEEE;
}
.settingPay .settingContent table tr td {
  height: 1.0667rem;
  border-bottom: 0.0267rem dashed #EEEEEE;
}
.settingPay .settingContent table tr td .checkBox {
  width: 0.4267rem;
  height: 0.4267rem;
  margin: 0 auto;
  border-radius: 0.1333rem;
}
.settingPay .settingContent table tr td .checkBox .icon-right {
  color: #6ED200;
  line-height: 0.4267rem;
}
.settingPay .settingContent .settingAll {
  width: 100%;
  height: auto;
}
.settingPay .settingContent .settingAll .allBox {
  width: 85%;
  margin: 0 auto;
  margin-top: 0.5333rem;
  padding-bottom: 0.32rem;
}
.settingPay .testButton {
  width: 44%;
  height: 0.9867rem;
  text-align: center;
  line-height: 0.9067rem;
  color: #FFF;
  font-size: 0.3733rem;
  background: #FF8902;
  border-radius: 0.1867rem;
  display: inline-block;
  margin-bottom: 0.2667rem;
  margin-left: 0.36rem;
}
.settingPay .botbtns {
  text-align: center;
  margin-top: -0.3rem;
  padding-bottom: 0.8rem;
}
.settingPay .botbtns .testButton {
  width: 80%;
}



