
.pik_calendar {
  width: 5rem;
  height: 1.2rem;
  margin: 0 auto;
}
.pik_calendar .arrows {
  float: left;
  width: 0.64rem;
  height: 0.64rem;
  margin: .28rem 0.15rem 0;
  vertical-align: middle;
  text-align: center;
  background: #F6F8F9;
  border-radius: 50%;
}
.pik_calendar .arrows .iconfont {
  font-size: .3rem;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  display: inline-block;
  line-height: .65rem;
  vertical-align: top;
}
.pik_calendar .calendar {
  width: 3.1rem;
  line-height: 1.2rem;
  float: left;
  text-align: center;
  font-size: .5rem;
}
.pik_calendar .grey {
  color: #D8D8D8;
}



