
.menulist .tableDetail {
  border: none;
  text-align: left;
  width: 94%;
  margin: 0 auto;
  border-bottom: 0.0267rem dashed #cccccc;
}
.menulist .tableDetail .contentDetail {
  height: 0.6467rem;
}
.menulist .tableDetail .contentDetail td {
  text-align: right;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.menulist .tableDetail .contentDetail td small {
  font-size: 0.2rem;
}
.menulist .tableDetail .contentDetail .dishname {
  width: 30%;
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 140px;
}
.menulist .tableDetail .contentDetail .subItem {
  padding-left: 0.5rem;
}
.menulist .tableDetail .contentDetail .rt {
  padding-right: 20px;
}
.menulist .tableDetail tr th {
  width: 28%;
  height: 1.2rem;
  border-bottom: 0.0267rem solid #cccccc;
  padding-left: 0.2667rem;
  text-align: right;
}



