
html,
body,
#app {
  height: 100%;
  position: relative;
}
.clear:after {
  content: "";
  display: block;
  clear: both;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}
.clear {
  zoom: 1;
}
.checkstand {
  height: 100%;
  position: relative;
}
.checkstand .topHeader {
  height: 40px;
}
.checkstand .topHeader .mintui-back {
  font-size: 0.64rem;
}
.checkstand .topHeader .mint-header {
  color: #fff;
  font-size: 16px;
  z-index: 100;
  background: #00487c !important;
}
.checkstand .checkstand-main {
  position: absolute;
  height: auto;
  top: 40px;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 5px 8px;
}
.checkstand .checkstand-main .des {
  text-align: left;
  color: #666666;
  font-size: 12px;
  line-height: 20px;
  height: 20px;
  margin: 0 0 3px 6px;
}
.checkstand .checkstand-main .screen {
  background: #fff;
  width: calc(100%);
  border-radius: 5px;
  position: relative;
  -webkit-box-shadow: 0px 0px 1px #999;
  box-shadow: 0px 0px 3px #999;
  height: 2.1rem;
}
.checkstand .checkstand-main .screen .category {
  display: inline-block;
  float: left;
  font-size: 14px;
  line-height: 1.6rem;
  height: 1.6rem;
  margin: 0 0 0 15px;
  color: #999;
  position: absolute;
  top: 0.2rem;
  left: 0.3rem;
  z-index: 10;
}
.checkstand .checkstand-main .screen .category i {
  padding: 2px 0 0 0;
  font-size: 12px;
}
.checkstand .checkstand-main .screen .active {
  color: #d8af68;
}
.checkstand .checkstand-main .screen .price-wrapper {
  background: #f7f7f7;
  border: 1px solid #eeeeee;
  border-radius: 5px;
  height: 1.4rem;
  position: absolute;
  top: 0.3rem;
  left: 3%;
  right: 0.2rem;
  width: 94%;
}
.checkstand .checkstand-main .screen .price {
  display: inline-block;
  float: right;
  font-size: 26px;
  line-height: 1.4rem;
  height: 1.4rem;
  margin: 0 10px 0 0;
  color: #cc7837;
  font-weight: bold;
  text-align: right;
  border: none;
  background: #f7f7f7;
  width: 90%;
}
.checkstand .checkstand-main .price:focus {
  outline: none;
}
.checkstand .checkstand-main .keyboard {
  position: absolute;
  height: auto;
  top: 3.2rem;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 0 0 0 0;
}
.checkstand .checkstand-main .keyboard .column {
  height: 25%;
  background: #fff;
  color: #999;
}
.checkstand .checkstand-main .keyboard .column ul:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #d9d9d9;
  color: #d9d9d9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.checkstand .checkstand-main .keyboard .column ul {
  height: 100%;
  position: relative;
}
.checkstand .checkstand-main .keyboard .column ul li {
  float: left;
  display: inline-block;
  width: 25%;
  height: 100%;
  position: relative;
  text-align: center;
}
.checkstand .checkstand-main .keyboard .column ul li:before {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-right: 1px solid #d9d9d9;
  color: #d9d9d9;
  -webkit-transform-origin: 100% 0;
  transform-origin: 100% 0;
  -webkit-transform: scaleX(0.5);
  transform: scaleX(0.5);
}
.checkstand .checkstand-main .keyboard .column ul li span {
  font-size: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -16px 0 0 -10px;
  line-height: 32px;
}
.checkstand .checkstand-main .keyboard .column ul li span.action {
  font-size: 18px;
  font-weight: normal;
  margin: -9px 0 0 -18px;
}
.checkstand .checkstand-main .keyboard .column2 {
  height: 50%;
  background: #fff;
  color: #999;
}
.checkstand .checkstand-main .keyboard .column2 .column2-left {
  display: inline-block;
  float: left;
  width: 75%;
  height: 100%;
  position: relative;
}
.checkstand .checkstand-main .keyboard .column2 .column2-left ul {
  height: 50%;
  position: relative;
}
.checkstand .checkstand-main .keyboard .column2 .column2-left ul:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-top: 1px solid #d9d9d9;
  color: #d9d9d9;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.checkstand .checkstand-main .keyboard .column2 .column2-left ul li {
  float: left;
  display: inline-block;
  width: 33.33333333%;
  height: 100%;
  position: relative;
  text-align: center;
}
.checkstand .checkstand-main .keyboard .column2 .column2-left ul li:before {
  content: " ";
  position: absolute;
  right: 0;
  top: 0;
  width: 1px;
  bottom: 0;
  border-right: 1px solid #d9d9d9;
  color: #d9d9d9;
  -webkit-transform-origin: 100% 0;
  transform-origin: 100% 0;
  -webkit-transform: scaleX(0.5);
  transform: scaleX(0.5);
}
.checkstand .checkstand-main .keyboard .column2 .column2-left ul li span {
  font-size: 32px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -16px 0 0 -10px;
  line-height: 32px;
}
.checkstand .checkstand-main .keyboard .column2 .column2-left ul li span.doubale-zero {
  margin: -16px 0 0 -16px;
}
.checkstand .checkstand-main .keyboard .column2 .column2-right {
  display: inline-block;
  float: right;
  width: 25%;
  background: #e3e3e3;
  height: 100%;
  position: relative;
}
.checkstand .checkstand-main .keyboard .column2 .column2-right p {
  font-size: 18px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -12px 0 0 -18px;
  font-weight: normal;
}
.checkstand .checkstand-main .keyboard .column2 .column2-right span {
  font-size: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 14px 0 0 -18px;
}
.checkstand .checkstand-main .keyboard .column2 .column2-right span.scan {
  margin: 14px 0 0 -30px;
}
.checkstand .checkstand-main .category-list {
  position: absolute;
  height: auto;
  top: 3.2rem;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 8px 10px 8px;
  padding: 13px 0;
  background: #ffffff;
  -webkit-box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
.checkstand .checkstand-main .category-list .category-tabs {
  width: 33.33%;
  float: left;
  display: inline-block;
  height: 100%;
  border-right: 1px solid #eeeeee;
}
.checkstand .checkstand-main .category-list .category-tabs ul {
  height: 100%;
  overflow: auto;
}
.checkstand .checkstand-main .category-list .category-tabs ul li {
  height: 14.28%;
  position: relative;
  vertical-align: middle;
}
.checkstand .checkstand-main .category-list .category-tabs ul li.active {
  background: #d8af68;
  color: #fff;
}
.checkstand .checkstand-main .category-list .category-tabs ul li.active span {
