  vertical-align: middle;
}
.checkstand .checkstand-main .category-list .category-content ul li.active span {
  color: #d8af68;
}
.checkstand .checkstand-main .category-list .category-content ul li:before {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #d9d9d9;
  color: #eeeeee;
  -webkit-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transform: scaleY(0.5);
  transform: scaleY(0.5);
}
.checkstand .checkstand-main .category-list .category-content ul li span {
  font-size: 14px;
  color: #000000;
  line-height: 67px;
}



