
.transactSearch {
  overflow-y: hidden;
}
.transactSearch .dateAll {
  width: 100%;
  height: 2.6rem;
  background: #fff;
  position: fixed;
  z-index: 2000;
}
.transactSearch .dateAll .dataBox {
  margin: 0 auto;
  width: 96%;
  height: 100%;
}
.transactSearch .dateAll .dataBox .data {
  width: 59%;
  height: 1.0667rem;
  display: inline-block;
}
.transactSearch .dateAll .dataBox .data .iconBox {
  border-radius: 2.6667rem;
  width: 0.8533rem;
  height: 0.8533rem;
  display: inline-block;
  text-align: center;
  background: #fff;
  -webkit-transform: none;
  transform: none;
}
.transactSearch .dateAll .dataBox .data .iconBox .icon-jiantou,
.transactSearch .dateAll .dataBox .data .iconBox .icon-caret-right-copy {
  font-size: 0.32rem;
  line-height: 0.88rem;
  color: #d8d8d8;
  -webkit-transform: scale(0.7);
          transform: scale(0.7);
}
.transactSearch .dateAll .dataBox .data .blakBg {
  background: #f6f8f9 !important;
  border-radius: 2.6667rem;
}
.transactSearch .dateAll .dataBox .data .fontBlack {
  color: black !important;
}
.transactSearch .dateAll .dataBox .data .dataDtail {
  display: inline-block;
  font-size: 0.3733rem;
  color: #393c41;
  margin: 0.2667rem 0.3733rem 0rem;
}
.transactSearch .dateAll .dataBox .line {
  width: 1px;
  text-align: center;
  display: inline-block;
  vertical-align: top;
  margin-top: 0.3rem;
  height: 0.5867rem;
  background: #cccccc;
}
.transactSearch .dateAll .dataBox .allBox {
  width: 38%;
  line-height: 1.04rem;
  height: 1.0667rem;
  display: inline-block;
  color: #393c41;
  font-size: 0.32rem;
  vertical-align: top;
}
.transactSearch .dateAll .searchBox {
  height: 1.6rem;
  margin: 0 auto;
  width: 100%;
  position: fixed;
  background: #f4f5f7;
  top: 2.2rem;
}
.transactSearch .dateAll .searchBox .search {
  width: 94%;
  margin: 0 auto;
  height: 0.8rem;
  padding-top: 0.4rem;
}
.transactSearch .dateAll .searchBox .search .tableNuber {
  width: 21%;
  height: 0.7733rem;
  font-size: 0.3733rem;
  padding-left: 0.2667rem;
  background: #fff;
  border: 0.0267rem solid #cccccc;
  border-radius: 0.1333rem;
  margin-left: 0.1333rem;
  float: left;
}
.transactSearch .dateAll .searchBox .search .orderNumer {
  width: 41%;
  height: 0.7733rem;
  font-size: 0.3733rem;
  padding-left: 0.2667rem;
  background: #fff;
  border: 0.0267rem solid #cccccc;
  border-radius: 0.1333rem;
  float: left;
  margin-left: 0.2667rem;
}
.transactSearch .dateAll .searchBox .search .searchResult {
  width: 20.5%;
  height: 0.7733rem;
  font-size: 0.3733rem;
  background: #d8af68;
  border-radius: 0.1867rem;
  float: right;
  color: #fff;
  line-height: 0.7467rem;
  margin-right: 0.08rem;
  margin-top: 0.0533rem;
}
.transactSearch .scrollBox {
  position: fixed;
  margin-top: 2.7rem;
  width: 98%;
  height: 79%;
  overflow-y: auto;
  padding-bottom: 2rem;
}
.transactSearch .scrollBox .orderList {
  background: #ffffff;
  margin-bottom: 0.2667rem;
  width: 96%;
  height: auto;
  padding-bottom: 0.2667rem;
  margin: 0 auto 0.2667rem;
  -webkit-box-shadow: 0 0 0.2133rem 0rem #dadada;
          box-shadow: 0 0 0.2133rem 0rem #dadada;
  border-radius: 0.1333rem;
  position: relative;
}
.transactSearch .scrollBox .orderList .mintui {
  position: absolute;
  top: 1.2rem;
  right: 0.05rem;
  color: #ccc;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.transactSearch .scrollBox .orderList .blueLind {
  height: 0.3733rem;
  width: 1.1%;
  background: #646f83;
  display: inline-block;
  float: left;
  margin-top: 0.48rem;
}
.transactSearch .scrollBox .orderList .returnPay {
  width: 94%;
  height: 1rem;
  border-top: 0.0267rem solid #eeeeee;
  margin: 0 auto;
}
.transactSearch .scrollBox .orderList .returnPay .returnButton {
  margin: 0 auto;
  width: 25%;
  background: #999;
  border-radius: 0.1867rem;
  color: #fff;
  line-height: 0.7467rem;
  margin-top: 0.2133rem;
}
.transactSearch .scrollBox .orderList .infoL {
  width: 49%;
  height: auto;
  text-align: left;
  display: inline-block;
  padding-top: 0.3733rem;
}
.transactSearch .scrollBox .orderList .infoL .lineInfo {
  margin-bottom: 0.1067rem;
  color: #999999;
}
.transactSearch .scrollBox .orderList .infoL .lineInfo .titleThird {
  display: inline-block;
  color: #999999;
  letter-spacing: 0.2667rem;
}
.transactSearch .scrollBox .orderList .infoL .lineInfo .titileS {
  display: inline-block;
  color: #999999;
}
.transactSearch .scrollBox .orderList .infoL .lineInfo .nomalL {
  letter-spacing: 0rem;
}
.transactSearch .scrollBox .orderList .infoL .lineInfo span {
  font-size: 0.32rem;
  color: #151515;
}
.transactSearch .scrollBox .orderList .infoR {
  width: 45%;
}
.transactSearch .scrollBox .orderList .infoBo {
  text-align: left;
  margin-left: 0.3733rem;
}
.transactSearch .scrollBox .orderList .infoBo .titleThird {
  display: inline-block;
  color: #999999;
  letter-spacing: 0.2667rem;
}
.transactSearch .scrollBox .orderList .infoBo .titileS {
  display: inline-block;
  color: #999999;
}
.transactSearch .scrollBox .orderList .infoBo .nomalL {
  letter-spacing: 0rem;
}
.transactSearch .scrollBox .orderList .infoBo span {
  margin-left: 0.1333rem;
  font-size: 0.3733rem;
  color: #151515;
}
.transactSearch .scrollBox .orderList .infoBo b {
  font-size: 0.32rem;
  color: #4990e2;
  text-decoration: underline;
  margin-left: 0.1333rem;
}
.nothing {
  width: 80px;
  height: 88px;
  margin: 150px auto 0 auto;
}
.nothingTxt {
  text-align: center;
  font-size: 20px;
  color: #ccd1d5;
  margin-top: 20px;
}
.show {
  display: block;
}
.hide {
  display: none;
}
.restaurantDetail_box {
  overflow: auto;
  height: 100%;
}
.restaurantDetail_box .DetailTop {
  width: 90%;
  height: 1.2533rem;
  margin: 0 auto;
  font-size: 0.32rem;
  color: #333333;
}
.restaurantDetail_box .DetailTop .topL {
  float: left;
  line-height: 1.2267rem;
}
.restaurantDetail_box .DetailTop .topR {
  float: right;
  line-height: 1.2267rem;
}
.restaurantDetail_box .DetailTop .topR span {
  display: inline-block;
}
.restaurantDetail_box .DetailTop .topR .circleBox {
  width: 0.5333rem;
  height: 0.5333rem;
  background: #646f83;
  border-radius: 1.36rem;
  color: #fff;
  display: inline-block;
  vertical-align: top;
  margin-top: 0.3733rem;
  line-height: 0.5867rem;
}



