
.opDetail_box {
  overflow: auto;
  height: 100%;
}
.opDetail_box .oDTop {
  width: 90%;
  height: 1.2533rem;
  margin: 1.1rem auto 0;
  font-size: 0.32rem;
  color: #333333;
}
.opDetail_box .oDTop .topL {
  float: left;
