  background: #646F83;
  border-radius: 1rem;
  color: #FFF;
  display: inline-block;
  vertical-align: top;
  margin-top: 0.3733rem;
  line-height: 0.54rem;
}
.opDetail_box .restaurantList {
  background: #FFFFFF;
  margin-bottom: 0.2667rem;
