  margin-left: 0.08rem;
}
.opDetail_box .restaurantList .listBox .vipCheckBox .infoBo span {
  font-weight: bold;
  margin-left: 0.1333rem;
  font-size: 0.3733rem;
  color: #151515;
}
.opDetail_box .restaurantList .clearBottom {
  border-bottom: none;
}
.opDetail_box .restaurantList .red {
  color: red;
}



