
.aversion .title {
  text-align: center;
  margin-top: 10px;
}
.aversion .title h1 {
  margin: 20px auto 5px;
  font-size: 0.5rem;
}
.aversion .title h2 {
  font-size: 0.3rem;
  font-weight: normal;
  margin-top: 0;
  color: #999;
}
.aversion .title img {
  width: 72px;
  margin: 0 auto 5px;
}
.aversion .tip {
  width: 90%;
  margin: 0 auto;
  margin-top: 1rem;
}
.aversion .tip li {
  line-height: 0.5rem;
  margin-top: 5px;
  text-align: left;
  color: #000;
}
.aversion .tip .pic img {
  width: 80%;
  margin: 0 auto;
}
.aversion .ver {
  position: fixed;
  bottom: 10px;
  left: 0;
  width: 100%;
  text-align: center;
}
.aversion .ver img {
  width: 63px;
  height: 18px;
  margin: 0 auto;
}
.aversion .ver div {
  font-size: 9px;
  color: #d7d7d7;
  margin-top: 8px;
}



