
.index {
  width: 100%;
  overflow: auto;
  height: 100vh;
  background: #003963;
}
.index .index_box {
  width: 96%;
  height: auto;
  margin: 0 auto;
}
.index .index_box .topLogo {
  margin-top: 0.2933rem;
  width: 100%;
  height: 2.6933rem;
  background: #00213f;
  border-radius: 0.1333rem;
  background-color: rgba(0, 0, 0, 0.3);
  text-align: center;
}
.index .index_box .topLogo .logofont {
  font-size: 0.8rem;
  color: #ffffff;
  line-height: 2.6667rem;
}
.index .index_box .vitem {
  margin-left: -2%;
}
.index .index_box .vitem li {
  float: left;
  width: 48%;
  margin-left: 2%;
}
.index .index_box .vitem li .orderPay {
  margin-top: 0.2rem;
  width: 100%;
  height: 3.7rem;
  border-radius: 0.1333rem;
}
.index .index_box .vitem li .orderPay .iconfont {
  font-size: 1.2rem;
  color: #ffffff;
  display: inline-block;
  width: 100%;
  margin-top: 0.4533rem;
}
.index .index_box .vitem li .orderPay .titleT {
  width: 100%;
  font-size: 0.55rem;
  color: #ffffff;
  margin-top: 0.2rem;
  float: left;
}



