
.settingPay {
  width: 100%;
  margin: 0 auto;
  text-align: left;
}
.settingPay .clearfix:before,
.settingPay .clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.settingPay .clearfix {
  zoom: 1;
}
.settingPay .titleS {
  width: 96%;
  margin: 0 auto;
  font-size: 0.32rem;
  display: block;
  height: 1.0667rem;
  color: #666666;
  line-height: 1.0667rem;
}
.settingPay .defaultNumber {
  -webkit-box-shadow: 0 0 0.2133rem 0rem #dadada;
          box-shadow: 0 0 0.2133rem 0rem #dadada;
  border-radius: 0.1333rem;
  width: 96%;
  margin: 0 auto;
  margin-top: 0.2667rem;
  background: #fff;
  padding-top: 0.0267rem;
  padding-bottom: 0.267rem;
}
.settingPay .defaultNumber h3 {
  font-size: 0.32rem;
  color: #666666;
  margin-top: 0.3733rem;
  margin-bottom: 0.1333rem;
  margin-left: 3.65%;
}
.settingPay .defaultNumber .switch {
  padding: 10px 0 0 0;
}
.settingPay .defaultNumber .switch label {
  float: left;
  margin-left: 20px;
  margin-right: 10px;
  line-height: 37px;
  height: 37px;
}
.settingPay .defaultNumber .inputNumber {
  width: 92.7%;
  margin: 0 auto;
  padding-bottom: 0.5333rem;
}
.settingPay .defaultNumber .inputNumber .workNumber {
  width: 90%;
  padding-left: 0.2667rem;
  border-radius: 0.1333rem;
  height: 0.9867rem;
  text-transform: uppercase;
  border: 0.0267rem solid #cccccc;
  margin: 0 auto;
}
.settingPay .settingContent {
  -webkit-box-shadow: 0 0 0.2133rem 0rem #dadada;
          box-shadow: 0 0 0.2133rem 0rem #dadada;
  border-radius: 0.1333rem;
  width: 96%;
  margin: 0 auto;
  height: auto;
  background: #fff;
}
.settingPay .settingContent table {
  margin: 0 auto;
  font-size: 0.3733rem;
  width: 96%;
}
.settingPay .settingContent table tr th {
  height: 1.36rem;
  border-bottom: 0.0267rem solid #eeeeee;
}
.settingPay .settingContent table tr td {
  height: 1.0667rem;
  border-bottom: 0.0267rem dashed #eeeeee;
}
.settingPay .settingContent table tr td .checkBox {
  width: 0.4267rem;
  height: 0.4267rem;
  margin: 0 auto;
  border-radius: 0.1333rem;
}
.settingPay .settingContent table tr td .checkBox .icon-right {
  color: #6ed200;
  line-height: 0.4267rem;
}
.settingPay .settingContent .settingAll {
  width: 100%;
  height: auto;
}
.settingPay .settingContent .settingAll .allBox {
  width: 85%;
  margin: 0 auto;
  margin-top: 0.5333rem;
  padding-bottom: 0.32rem;
}
.settingPay .settingContent .settingAll .allBox .testButton {
  width: 44%;
  height: 0.9867rem;
  text-align: center;
  line-height: 0.9067rem;
  color: #fff;
  font-size: 0.3733rem;
  background: #ff8902;
  border-radius: 0.1867rem;
  display: inline-block;
  margin-bottom: 0.2667rem;
  margin-left: 0.36rem;
}



