
.sysadmin {
  width: 100%;
  margin: 0 auto;
  text-align: left;
}
.sysadmin .clearfix:before,
.sysadmin .clearfix:after {
  content: "";
  display: block;
  clear: both;
}
.sysadmin .clearfix {
  zoom: 1;
}
.sysadmin .defaultNumber {
  -webkit-box-shadow: 0 0 0.2133rem 0rem #dadada;
          box-shadow: 0 0 0.2133rem 0rem #dadada;
  border-radius: 0.1333rem;
  width: 96%;
  margin: 0 auto;
  margin-top: 0.2667rem;
  background: #fff;
  padding-top: 0.0267rem;
  padding-bottom: 0.267rem;
}
.sysadmin .defaultNumber h3 {
  font-size: 0.32rem;
  color: #666666;
  margin-top: 0.3733rem;
  margin-bottom: 0.1333rem;
  margin-left: 3.65%;
}
.sysadmin .defaultNumber .inputNumber {
  width: 92.7%;
  margin: 0 auto;
  padding-bottom: 0.5333rem;
}
.sysadmin .defaultNumber .inputNumber .workNumber {
  width: 90%;
  padding-left: 0.2667rem;
  border-radius: 0.1333rem;
  height: 0.9867rem;
  text-transform: uppercase;
  border: 0.0267rem solid #cccccc;
  margin: 0 auto;
}
.sysadmin .settingContent {
  -webkit-box-shadow: 0 0 0.2133rem 0rem #dadada;
          box-shadow: 0 0 0.2133rem 0rem #dadada;
  border-radius: 0.1333rem;
  width: 96%;
  margin: 0 auto;
  height: auto;
  background: #fff;
}
.sysadmin .settingContent .settingAll {
  width: 100%;
  height: auto;
}
.sysadmin .settingContent .settingAll .allBox {
  margin: 0 auto;
  margin-top: 0.5333rem;
  padding: 0.32rem 0;
}
.sysadmin .testButton {
  width: 44%;
  height: 0.9867rem;
  text-align: center;
  line-height: 0.9067rem;
  color: #fff;
  font-size: 0.3733rem;
  background: #ff8902;
  border-radius: 0.1867rem;
  display: inline-block;
  margin-bottom: 0.2667rem;
  margin-left: 0.36rem;
}



