
.adminpass {
  opacity: 0.7;
  background: #000000;
  width: 100%;
  height: 100%;
}
.adminpass .bgBox {
  height: 3rem;
}
.adminpass .contentBox {
  width: 80%;
  margin: 0 auto;
  background: #ffffff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 0.3rem  0.3rem 1rem;
}
.adminpass .contentBox .top {
  width: 92%;
  height: 1.1467rem;
  margin: 0 auto;
}
.adminpass .contentBox .top span {
  font-size: 0.32rem;
  color: #333333;
  float: left;
  margin-top: 0.3467rem;
  font-weight: bold;
}
.adminpass .contentBox .top .icon-cuowu {
  font-size: 0.32rem;
  color: #999999;
  float: right;
  margin: 0.2667rem 0.2rem;
}
.adminpass .contentBox .inputTwo {
  height: 2.2rem;
  width: 76.7%;
  margin: 0.6rem auto;
}
.adminpass .contentBox .inputTwo .inp {
  width: 100%;
  height: 0.9867rem;
  text-transform: uppercase;
  background: #ffffff;
  border: 0.0267rem solid #cccccc;
  border-radius: 0.1333rem;
  margin-top: 0.2933rem;
  padding-left: 0.3733rem;
}
.adminpass .contentBox .twoSure {
  font-size: 0.3733rem;
  color: #ffffff;
  width: 45%;
  height: 0.9867rem;
  line-height: 0.9067rem;
  background: #ff8902;
  border-radius: 0.1867rem;
  text-align: center;
  display: block;
  margin: 0 27%;
}



